const networkHelper = {
  async fetchHelper(uri, requestBody = {}) {
    const requestParameters = {
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      ...requestBody,
    };
    let result;
    const uriWithToken = `${uri}?access_token=${localStorage.getItem('token')}`;
    try {
      result = await (await fetch(uriWithToken, requestParameters)).json();
    } catch (error) {
      console.log(error);
    }

    // todo: change to result.error
    if (result.error) {
      console.log(result);
      Promise.reject(result.message);
    }
    return Promise.resolve(result);
  },
};

export default networkHelper;



