import React, { Component } from 'react';
import { Redirect, Link } from 'react-router-dom';
import authHelper from '../services/auth-helper';
import FormGroup from './FormGroup';

class Login extends Component {
  state = {
    redirectToReferrer: false,
  };

  onSubmit = async e => {
    e.preventDefault();
    const { username, password } = e.target;

    const resultsObj = {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        username: username.value,
        password: password.value,
      }),
    };

    const result = await (await fetch(`${process.env.REACT_APP_API_ENDPOINT}/api/Users/login`, resultsObj)).json();
    console.log(result);
    if (result.id) {
      console.log(result.id);
      authHelper.authenticate(result.id).then(() => this.setState({ redirectToReferrer: true }));
    } else {
      this.setState({ msg: 'Login Failed: Please check your credentials', msgClass: 'alert-danger' });
    }
  };

  render() {
    const { from } = this.props.location.state || { from: { pathname: '/' } };
    const { redirectToReferrer } = this.state;

    if (redirectToReferrer) {
      return <Redirect to={from} />;
    }
    return (
      <div id="login">
        <div className="row">
          <div className="col-12">
            <h1 className="text-center mb-3">Habit Tracker</h1>
          </div>
        </div>
        <div className="row justify-content-center">
          <div className="col-md-8">
            <form className="card-box" onSubmit={this.onSubmit}>
              <h1 className="text-center">Login</h1>
              {this.state.msg && <div className={`alert ${this.state.msgClass}`}>{this.state.msg}</div>}
              <FormGroup>
                <label htmlFor="username">Username</label>
                <input type="text" className="form-control" id="username" placeholder="Enter Username" />
              </FormGroup>
              <FormGroup>
                <label htmlFor="password">Password</label>
                <input type="password" className="form-control" id="password" placeholder="Password" />
              </FormGroup>
              <div className="row">
                <div className="col-md-3">
                  <Link to="/demo" className="demo-link text-dark">
                    Demo Mode
                  </Link>
                </div>
                <div className="col-md-3 ml-auto">
                  <button type="submit" className="ml-auto btn btn-primary">
                    Submit
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;



