import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';

const Navbar = ({ openSideBar, sideBarState, handleLogout, toggle }) => (
  <Fragment>
    <button className="custom-button" onClick={() => openSideBar(!sideBarState)}>
      {sideBarState ? 'X' : '>'}
    </button>
    <nav className="navbar navbar-expand-lg navbar-bg-color navbar-lg navbar-dark">
      <button className="mr-2" onClick={() => toggle()}>
        <i className="fas fa-lightbulb" />
      </button>
      <Link to="/" className="navbar-brand">
        Habit Tracker
      </Link>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon" />
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="ml-auto navbar-nav">
          <li className="nav-item">
            <a className="nav-link" href="" onClick={() => handleLogout()}>
              Logout
            </a>
          </li>
        </ul>
      </div>
    </nav>
  </Fragment>
);

export default Navbar;



